import React, { useState, useEffect, useCallback } from "react";
import "./App.css";
// import logo from "./assets/logo.png";
import Download from "./assets/download.png";

function App() {
  const API_KEY = "ndFZWMqcwlbe4uaEQAjp48nuA7t17Agu18kaGyieUpXK5UIDUEqsGVvl";
  const [images, setImages] = useState([]);
  const [pageIndex, setPageIndex] = useState(1);
  const [searchValueGlobal, setSearchValueGlobal] = useState("");

  const fetchImages = useCallback(async (url) => {
    try {
      const response = await fetch(url, {
        headers: {
          Accept: "application/json",
          Authorization: API_KEY,
        },
      });
      return await response.json();
    } catch (error) {
      console.error("Error fetching images:", error);
      return { photos: [] };
    }
  }, []);

  const getImages = useCallback(async (index) => {
    const data = await fetchImages(
      `https://api.pexels.com/v1/curated?page=${index}&per_page=12`
    );
    setImages(data.photos || []);
  }, [fetchImages]);

  const getSearchedImages = useCallback(async (searchValue, index = 1) => {
    const data = await fetchImages(
      `https://api.pexels.com/v1/search?query=${searchValue}&page=${index}&per_page=12`
    );
    if (index === 1) {
      setImages(data.photos || []);
    } else {
      setImages(prev => [...prev, ...(data.photos || [])]);
    }
  }, [fetchImages]);

  useEffect(() => {
    if (searchValueGlobal) {
      getSearchedImages(searchValueGlobal, pageIndex);
    } else {
      getImages(pageIndex);
    }
  }, [pageIndex, searchValueGlobal, getImages, getSearchedImages]);

  const handleSearch = (e) => {
    e.preventDefault();
    const searchValue = e.target.querySelector("input").value;
    setPageIndex(1);
    setSearchValueGlobal(searchValue);
    e.target.reset();
  };

  const handleLoadMore = () => {
    setPageIndex(prev => prev + 1);
  };

  const renderImage = useCallback(({ id, src, photographer }) => (
    <div className="item" key={id}>
      <a href={src.original} data-lightbox="mygallary" data-title={photographer}>
        <img src={src.large} alt={photographer} />
        <h3>{photographer}</h3>
      </a>
      <a href={src.original} target="_blank" download rel="noopener noreferrer">
        <img className="photo-download_info" src={Download} alt="Download" />
      </a>
    </div>
  ), []);

  return (
    <section>
      <div className="container">
        <header className="header">
          {/* <img src={logo} alt="logo" /> */}
          <h1>Art-Gallary</h1>
          <form onSubmit={handleSearch}>
            <input type="text" placeholder="Search" />
          </form>
        </header>
        <div className="gallery">{images.map(renderImage)}</div>
        <button className="load-more" onClick={handleLoadMore}>
          Load More
        </button>
      </div>
    </section>
  );
}

export default App;
